<template>
  <!--负荷转换潜力分析-->
    <div>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="负荷状态" name="first"></el-tab-pane>
        <el-tab-pane label="转换分析" name="second"></el-tab-pane>
      </el-tabs>
      <div>
        <TreeOrg :treeData="treeData" style="float: left"></TreeOrg>
    <div style="float: left;width: calc(100% - 260px);margin-left: 20px;">
      <div class="div-top">
        <span style="margin-left: 30px;">时间段选择</span>
        <el-date-picker style="margin-left: 5px;"
                        v-model="yeardate"
                        @change="changeDate"
                        type="date"
                        placeholder="选择日期">
        </el-date-picker>
      </div>
      <div class="div-second">
        <div id="chart_load" style="width: 100%;height: 100%;"></div>
      </div>
      <div class="div-third"  v-if="viewdiv=='first'">
        <vTable ref="vTable"></vTable>
      </div>
      <div class="div-third-1">
        <!--通过ref调用子组件vTransanalysis里面的方法-->
          <v-Transanalysis  v-if="viewdiv=='second'" ref="vTransanalysis"></v-Transanalysis>
        <!--<div class="div-table" style="height: 100%; width: calc(50% - 10px);float: left;margin-top: 50px">-->
          <!--<p class="table-title-p"></p>-->
          <!--<el-table-->
            <!--:data="totalTableData"-->
            <!--border-->
            <!--:header-cell-style="totalheadercellstyle"-->
            <!--style="width: 100%;margin-top: 10px;">-->
            <!--<el-table-column-->
              <!--prop="name"-->
              <!--label="名称">-->
            <!--</el-table-column>-->
            <!--<el-table-column-->
              <!--prop="currload"-->
              <!--label="实际">-->
            <!--</el-table-column>-->
            <!--<el-table-column-->
              <!--prop="maxload"-->
              <!--label="最大">-->
            <!--</el-table-column>-->
            <!--<el-table-column-->
              <!--prop="minload"-->
              <!--label="最小">-->
            <!--</el-table-column>-->
          <!--</el-table>-->
        <!--</div>-->
        <!--<div id="chart_avgload"   style="height: 500px; width:700px;margin-left: 20px;float: left;"></div>-->
      </div>
    </div>
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
  import vTransanalysis from "./Transanalysis.vue";
  import vTable from "./energyChangeTable.vue";
  import TreeOrg from "../../common/TreeOrg.vue";
    export default {
        data () {
            return {
              viewdiv:'first',
              activeName: 'first',
              yeardate:new Date(),

              pos:0,
              isA:true,
              chartwidth:200,
              treeData: [
                {
                label: '斗门区 (12)',
                children: [{
                  label: '珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)',
                },
                  {
                    label: '珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)',
                  },
                  {
                    label: '珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)',
                  }]
              }, {
                label: '金湾区 (6)',
                children: [{
                  label: '二级 2-1',
                  children: [{
                    label: '三级 2-1-1'
                  }]
                }, {
                  label: '二级 2-2',
                  children: [{
                    label: '三级 2-2-1'
                  }]
                }]
              }, {
                label: '横琴新区 (6)',
                children: [{
                  label: '二级 3-1',
                  children: [{
                    label: '三级 3-1-1'
                  }]
                }, {
                  label: '二级 3-2',
                  children: [{
                    label: '三级 3-2-1'
                  }]
                }]
              }],

            }
        },

      mounted() {
        let  that=this;

        that.getchart_dayloadData();
        that.autoHeight();
      },

      methods: {
        changeDate(){
          let  that=this;
          console.log(that.viewdiv)
          //通过$refs可以直接调用子组件vTransanalysis里面的方法
          if(that.viewdiv == 'first'){
            that.$refs.vTable.getEnergyChangeData();
          }else if(that.viewdiv == 'second'){
            that.$refs.vTransanalysis.getAllMinMaxData();
          }

          that.getchart_dayloadData();
        },
        autoHeight() {
          let  that=this;
//          var height = document.body.clientHeight-200;
          var clientWidth = document.body.clientWidth;
          that.chartwidth=clientWidth*0.2-10;

        },
        handleClick(tab, event) {
          this.activeName=tab.name;
          this.viewdiv=tab.name;
          if(tab.name=="second"){
            var clientWidth = document.body.clientWidth;
            this.chartwidth=clientWidth*0.2-10;
//            this.initchart_avgload();
          }
        },



        renderHeader(h, { column, $index }) {
          let label = column.label;
          let labelArr = label.split(' ');
          return (
            <div>
              <span>2222</span>
              <p>11111</p>
            </div>
          )

//          return h(
//            'div',
//            {
//              'class': 'header-center'
//            },
//            [
//              h('div', {
//                  attrs: { type: 'text' },
//                }, [labelArr[0]]
//              ),
//              h('div', {
//                  attrs: { type: 'text' },
//                }, [labelArr[1] || '']
//              )
//            ]
//          );
        },

        getchart_dayloadData(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/load/getLoadByDay", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.initchart_dayloadData(data);

              }
            });
        },
        initchart_dayloadData(data){
          let  that=this;
          if(data){
            var xdata=[];
            var ydatas=[];
            var legend=[];
            var index=0;
            for(var item of data){
              var ydata={};
              ydata.id=item.id;
              ydata.name=item.name;
              ydata.data=[];
              legend.push(item.name);
              for(var yda of item.data){
                ydata.data.push(yda.value);
                if(index==0){
                  xdata.push(yda.time);
                }

              }
              ydatas.push(ydata);
              index++;
            }

            var series=[];
            for(var ydata of ydatas){
              series.push({
                name:ydata.name,
                type:'line',
                stack: '负荷',
                showSymbol: false,
                data:ydata.data
              });
            }
            that.initchart_load(xdata,legend,series);

          }
        },
        initchart_load(xdata,legend,series){
          var option = {
            title: {
              text: '日多能负荷曲线'
            },
            tooltip: {
              trigger: 'axis'
            },
            color:['#0A6EDA','#FF081C','#0C9CA0'],
            legend: {
              itemWidth: 20,
              itemHeight: 3,
              icon:'bar',
              data:legend
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '3%',
              containLabel: true
            },
//            toolbox: {
//              feature: {
//                saveAsImage: {}
//              }
//            },
            xAxis: {
              type: 'category',
              boundaryGap: false,
              data: xdata

            },
            yAxis: [
              {
                type: 'value',
                name: 'kW',
                axisLabel: {
                  formatter: '{value}'
                }
              },
              {
                type: 'value',
                name: 'kWh',
                axisLabel: {
                  formatter: '{value}'
                }
              }
            ],
            series: series
          };
          var myChart = echarts.init(document.getElementById("chart_load"));
          myChart.setOption(option);
        },

        initchart_avgload(){
          var option = {
            // title: {
            //     text: '世界人口总量',
            //     subtext: '数据来自网络'
            // },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              }
            },
            color:['#0A6EDA','#FF9900','#DCDFE6'],
            legend: {
              itemWidth: 20,
              itemHeight: 4,
              icon:'bar',
              y:'bottom',
              data: ['最小', '实际','最大']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '5%',
              containLabel: true
            },
            xAxis: {
              type: 'value',
              boundaryGap: [0, 0.01]
            },
            yAxis: {
              type: 'category',
              data: ['电','热','冷','燃气']
            },
            series: [
              {
                name: '最小',
                type: 'bar',
                stack: '负荷',
                barWidth: 30,
                data: [18203, 23489, 29034, 104970]
              },
              {
                name: '实际',
                type: 'bar',
                stack: '负荷',
                data: [19325, 23438, 31000, 121594]
              }
              ,
              {
                name: '最大',
                type: 'bar',
                stack: '负荷',
                data: [121594, 134141, 31000, 121594]
              }
            ]
          };
          var myChart = echarts.init(document.getElementById("chart_avgload"));
          myChart.setOption(option);
        },

      },
      components: {
        vTransanalysis,
        TreeOrg,
        vTable
      },

    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .class-a{
    height: 100%; width: calc(50% - 10px);
  }
  .div-second{
    width: 100%;
    height: 450px;
    margin-top: 30px;
  }
  .div-third{
    width: 100%;
    height: 500px;
    margin-top: 30px;
    /*background-color: #20a0ff;*/
  }
  .div-third-1{
    width: 100%;
    height: 500px;
    margin-top: 30px;
    /*background-color: #20a0ff;*/
  }
  .table-title-p{
    font-size: 18px;
    font-weight: bold;
    float: left;
  }

  .item-title{
    font-weight: bold;
    font-size: 14px;
    color: #101010;
  }
  .item-text{

  }
  .item-value{
    font-size: 16px;
    font-weight: bold;
    color: #1567B3;
  }
  .group-item{
     width: 100%;
    height: 20px;
  }
  .scope-icon-1{
    width: 60px;
    height: 12px;
    background-color: #0A6EDA;
    border: 1px solid  #0A6EDA;
    float: left;
    margin-top: 3px;
    margin-left: 5px;
  }
  .scope-icon-2{
    width: 60px;
    height: 12px;
    background-color: #9254DE;
    border: 1px solid  #9254DE;
    float: left;
    margin-top: 3px;
    margin-left: 5px;
  }
  .scope-icon-3{
    width: 60px;
    height: 12px;
    background-color: #0c9ca0;
    border: 1px solid  #0c9ca0;
    float: left;
    margin-top: 3px;
    margin-left: 5px;

  }
  .div-table{
    width: calc(50% - 10px);
    height: 100%;
    float: left;
  }
  .table-title-p{
    margin-bottom: 15px;
    font-size: 14px;
    font-weight: bold;

  }
</style>



