              CCHPChartData:[],
            }
        },
      created(){
        let  that=this;
        that.getEnergyChangeData();
      },
      mounted(){},
      updated(){
        var that = this;
        that.initpowerChartPie(that.powerChartData);
        that.inithotChartPie(that.hotChartData);
        that.initcoldChartPie(that.coldChartData);
        that.initCCHPChartPie(that.CCHPChartData);
      },
      methods:{
        arraySpanMethod({ row, column, rowIndex, columnIndex }) {
          if (columnIndex === 0) {
            const _row = this.spanArr[rowIndex];
            const _col = _row > 0 ? 1 : 0;
            return {
              rowspan: _row,
              colspan: _col
            }
          }
//          if (columnIndex === 0) {
//            if (rowIndex % 2 === 0) {
//              return {
//                rowspan: 2,
//                colspan: 1
//              };
//            } else {
//            return {
//              rowspan: 0,
//              colspan: 0
//            };
//          }
//          }
        },
        cellstyle({row, column, rowIndex, columnIndex}){
          if(columnIndex ===0){
            return 'background: #9BC2E6;';
          }
          if(columnIndex ===1){
            return 'background: #DDEBF7;';
          }
        },
        headerrowstyle({row, rowIndex}){
          if(rowIndex ===0  ){
            return 'background: #A9D08E;';
          }
        },
        headercellstyle({row, column, rowIndex, columnIndex}){
          if(rowIndex ===0 && columnIndex !=0 && columnIndex !=1){
            return 'background: #A9D08E;';
          }
          if(rowIndex ===1){
            return 'background: #E2EFDA;';
          }
        },
        totalheadercellstyle({row, column, rowIndex, columnIndex}){
          return 'background:#E4E7EB;font-weight: bold;color: #101010;';
        },
        getEnergyChangeData (){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/load/getEnergyChange", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.tableData = data;
                that.getSpanArr(that.tableData);
              }
            });
        },
        getSpanArr(data) {
          console.log(data);
          let that = this;
          for (var i = 0; i < data.length; i++) {
            if (i === 0) {
              this.spanArr.push(1);
              this.pos = 0
            } else {
              // 判断当前元素与上一个元素是否相同
              if (data[i].title === data[i - 1].title) {
                this.spanArr[this.pos] += 1;
                this.spanArr.push(0);
              } else {
                this.spanArr.push(1);
                this.pos = i;
              }
            }
            if(data[i].chartId == "powerChartPie"){
              that.powerChartData = [];
              that.powerChartData.push(
                {value:data[i].electricvalue, name:'电'},
                {value:data[i].heatvalue, name:'热'},
                {value:data[i].coldvalue, name:'冷'}
              )
            }else if(data[i].chartId == "hotChartPie"){
              that.hotChartData = [];
              that.hotChartData.push(
                {value:data[i].electricvalue, name:'电'},
                {value:data[i].heatvalue, name:'热'},
                {value:data[i].coldvalue, name:'冷'}
              )
            }else if(data[i].chartId == "coldChartPie"){
              that.coldChartData = [];
              that.coldChartData.push(
                {value:data[i].electricvalue, name:'电'},
                {value:data[i].heatvalue, name:'热'},
                {value:data[i].coldvalue, name:'冷'}
              )
            }else if(data[i].chartId == "CCHPChartPie"){
              that.CCHPChartData = [];
              that.CCHPChartData.push(
                {value:data[i].electricvalue, name:'电'},
                {value:data[i].heatvalue, name:'热'},
                {value:data[i].coldvalue, name:'冷'}
              )
            }

          }
          console.log(that.powerChartData);
        },
        initpowerChartPie(data){
          var option = {
            tooltip : {
              trigger: 'item',
              formatter: "{a} <br/>{b} : {c} ({d}%)"
            },
            series : [
              {
                name: '',
                type: 'pie',
                radius : '55%',
                center: ['50%', '50%'],
                data:data,
                label:{
                  normal: {
                    //formatter: '{b|{b}：}{c}  {per|{d}%}  ',
                    formatter: '{b|{b}}{per|{d}%}',
                    backgroundColor: '#eee',
                    borderColor: '#aaa',
                    borderWidth: 1,
                    borderRadius: 4,
                    // shadowBlur:3,
                    // shadowOffsetX: 2,
                    // shadowOffsetY: 2,
                    // shadowColor: '#999',
                    // padding: [0, 7],
                    rich: {
                      a: {
                        color: '#999',
                        lineHeight: 22,
                        align: 'center'
                      },
                      // abg: {
                      //     backgroundColor: '#333',
                      //     width: '100%',
                      //     align: 'right',
                      //     height: 22,
                      //     borderRadius: [4, 4, 0, 0]
                      // },
                      hr: {
                        borderColor: '#aaa',
                        width: '100%',
                        borderWidth: 0.5,
                        height: 0
                      },
                      b: {
                        padding:[5,5],
                        fontSize: 12,
                        lineHeight: 12
                      },
                      per: {
                        color: '#eee',
                        backgroundColor: '#334455',
                        padding: [2, 4],
                        borderRadius: 2,
                        align: 'center',
                      }
                    }
                  }
                },
                itemStyle: {
                  emphasis: {
                    shadowBlur: 10,
                    shadowOffsetX: 0,
                    shadowColor: 'rgba(0, 0, 0, 0.5)'
                  }
                }
              }
            ]
          };
          var myChart = echarts.init(document.getElementById("powerChartPie"));
          myChart.setOption(option);
        },
