                center: ['50%', '50%'],
                data:data,
                label:{
                  normal: {
                    //formatter: '{b|{b}：}{c}  {per|{d}%}  ',
                    formatter: '{b|{b}}{per|{d}%}',
                    backgroundColor: '#eee',
