    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .class-a{
    height: 100%; width: calc(50% - 10px);
  }
  .div-second{
    width: 100%;
    height: 450px;
    margin-top: 30px;
  }
  .div-third{
    width: 100%;
