<template>
    <div>
      <div class="div-table" style="height: 100%; width: calc(50% - 10px);float: left;margin-top: 50px">
        <p class="table-title-p"></p>
        <el-table
          :data="totalTableData"
          border
          :header-cell-style="totalheadercellstyle"
          style="width: 100%;margin-top: 10px;">
          <el-table-column
            prop="name"
            label="名称">
          </el-table-column>
          <el-table-column
            prop="currload"
            label="实际">
          </el-table-column>
          <el-table-column
            prop="maxload"
            label="最大">
          </el-table-column>
          <el-table-column
            prop="minload"
            label="最小">
          </el-table-column>
        </el-table>
      </div>
      <div id="chart_avgload"   style="height: 500px; width: calc(50% - 10px);margin-left: 20px;float: left;"></div>
    </div>
</template>

<script>
  import echarts from "echarts";
    export default {
        data () {
            return {
              totalTableData:[],
              oldDate:''
            }
        },
      mounted() {
        let  that=this;
        that.oldDate = that.yeardate;
        console.log(that.oldDate);
        if(that.oldDate != that.yeardate){
          alert(1);
        }
        that.initchart_avgload();
        that.getAllMinMaxData();
      },
      methods: {
        totalheadercellstyle({row, column, rowIndex, columnIndex}){
          return 'background:#E4E7EB;font-weight: bold;color: #101010;';
        },
        getAllMinMaxData(){
          let that = this;
          that.$http
            .get(this.BASE_INFO.BASE_URL+"api/load/getAllMinMaxTable", {})
            .then(function(response) {
              if(response && response.data && response.data.data){
                var  data=response.data.data;
                that.initAllMinMaxData(data);

              }
            });
        },
        initAllMinMaxData(data){
          let  that=this;
          if(data){
            that.totalTableData = data;
            var currloadArr = [];
            var maxloadArr = [];
            var minloadArr = [];
            for(var item of data){
              currloadArr.push(item.currload);
              maxloadArr.push(item.maxload);
              minloadArr.push(item.minload);
            }
            that.initchart_avgload(currloadArr,maxloadArr,minloadArr)
          }
        },
        initchart_avgload(currloadArr,maxloadArr,minloadArr){
          var option = {
            // title: {
            //     text: '世界人口总量',
            //     subtext: '数据来自网络'
            // },
            tooltip: {
              trigger: 'axis',
              axisPointer: {
                type: 'shadow'
              }
            },
            color:['#0A6EDA','#FF9900','#DCDFE6'],
            legend: {
              itemWidth: 20,
              itemHeight: 4,
              icon:'bar',
              y:'bottom',
              data: ['最小', '实际','最大']
            },
            grid: {
              left: '3%',
              right: '4%',
              bottom: '5%',
              containLabel: true
            },
            xAxis: {
              type: 'value',
              boundaryGap: [0, 0.01]
            },
            yAxis: {
              type: 'category',
              data: ['电','热','冷','燃气']
            },
            series: [
              {
                name: '最小',
                type: 'bar',
                stack: '负荷',
                barWidth: 30,
                data: minloadArr
              },
              {
                name: '实际',
                type: 'bar',
                stack: '负荷',
                data: currloadArr
              }
              ,
              {
                name: '最大',
                type: 'bar',
                stack: '负荷',
                data: maxloadArr
              }
            ]
          };
          var myChart = echarts.init(document.getElementById("chart_avgload"));
          myChart.setOption(option);
        },
      },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



